<template lang="pug">
div
   .factorian-content-block
      .container
         slot(name="titulo")
         .row
            .col-sm-3(v-for="episodio in data")
               .single-service-item.wow.fadeInUp
                  .foto(:style="{background:'url(http://img.youtube.com/vi/' + getId(episodio.video) + '/0.jpg)'}")           
                  h3.titulovideo {{ episodio.titulo }}
                  p en: {{ episodio.programa.titulo }}
                  a.service-readmore-btn(@click="verepisodio(episodio)")  
                     span ver episodio 
                     i.fa.fa-long-arrow-right

   

</template>


<script>
import { getIdFromURL } from 'vue-youtube-embed'
export default {
   props: ['data'],
  
   methods: {
      getId (url) {
         return this.$youtube.getIdFromURL(url)
      },
      verepisodio(episodio){
			this.$router.push({ name: 'verEpisodio', params: { id: episodio.id }})
		},
   }
}

</script>

<style>
h3.titulovideo {
    font-size: 18px;
}

</style>



