<template lang="pug">

.carousel.slide(id="carousel-example-generic" data-ride="carousel")
	
	.carousel-inner(role="listbox" )
		.item(v-for="(item,key) in data" :class="{ active: key==0 }")
			.single-slide-item.slide-bg-1
				.slide-item-table
					.slide-item-tablecell
						.container
							.row
								.col-md-6
									h2 {{ item.titulo }}
									p {{ item.bajada }}

								.col-md-5
									img(:src="Global.url + 'uploads/post/' +item.imagen" ,style={height:'300px', width:'400px'} ) 

	
	a.left.carousel-control(href="#carousel-example-generic" role="button" data-slide="prev")
		span.glyphicon.glyphicon-chevron-left
		
	a.right.carousel-control(href="#carousel-example-generic" role="button" data-slide="next")
		span.glyphicon.glyphicon-chevron-right

             

</template>

<script>
import { mapState } from 'vuex'
export default {
   props: ['data'],
   computed: {
      ... mapState(['Global']) 
   },
	mounted () {
      $(document).ready(function(){
         
         $(".carousel-inner").on("translate.item", function(){
            $(".single-slide-item h2, .single-slide-item p").removeClass("animated fadeInUp").css("opacity", "0");
            $(".single-slide-item .slide-btn").removeClass("animated fadeInDown").css("opacity", "0");
         });
         
         $(".carousel-inner").on("translated.item", function(){
            $(".single-slide-item h2, .single-slide-item p").addClass("animated fadeInUp").css("opacity", "1");
            $(".single-slide-item .slide-btn").addClass("animated fadeInDown").css("opacity", "1");
         });
      });/*
      */
      
      
      /* $(".gallery-lightbox").magnificPopup({
         type: 'image',
         gallery: {
               enabled: true
         }
      });
      
      $("ul#navigation").slicknav({
         prependTo: ".responsive-menu-wrap"
      }); */
      
      
      //new WOW().init();

      /* jQuery(window).load(function(){
         jQuery(".factorian-slide-preloader-wrap, .factorian-site-preloader-wrap").fadeOut(500);
      }); */

	}
}

</script>

<style>
.single-slide-item h2 {
    font-size: 49px;
    margin: 0px 0 30px 35px;
}

.slide-item-table p {
    margin: 0 0 10px 35px;
	 font-size: 16px;
}
</style>


