<template lang="pug">
div
   //- Slider
   modulo-slider(:data="Global.noticias.ultimas" v-if="Global.noticias.ultimas.length > 0")
   cuatro-episodios(:data="Global.episodios.ultimos1" v-if="Global.episodios.ultimos1.length > 0")
   modulo-slider2(:data="Global.noticias.ultimas" v-if="Global.noticias.ultimas.length > 0")
   cuatro-episodios(:data="Global.episodios.ultimos2" v-if="Global.episodios.ultimos2.length > 0")

</template>


<script>
import { mapState } from 'vuex'
import { getIdFromURL } from 'vue-youtube-embed'
import CuatroEpisodios from './../components/cuatroEpisodios.vue'
import ModuloSlider from './../components/slider.vue'
import ModuloSlider2 from './../components/slider2.vue'
export default {
   components: {
      CuatroEpisodios,
      ModuloSlider,
      ModuloSlider2
   },
   computed: {
      ... mapState(['Global']) 
   },
   created () {
      this.$store.dispatch('Global/NOTICIAS_ULTIMAS')
      //this.$store.dispatch('Global/SLIDER_PUBLI')
   }
}

</script>


